import { useState } from "react";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import { useTranslation } from "react-i18next";
import {
  useInstituteAcademicSelectBatch,
  useInstituteDepartmentDashboard,
} from "../../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";

const InstituteAcademicDepartmentBatchCardMenu = ({
  onClose,
  menuData,
  did,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [instituteAcademicSelectBatch] = useInstituteAcademicSelectBatch();
  const { instituteDepartmentDashboardRefetch } =
    useInstituteDepartmentDashboard({
      data: {
        did: did,
      },
      skip: !did,
    });
  const onSelectBatch = () => {
    if (menuData?.u_batch?._id && did) {
      setDisabled((pre) => !pre);
      instituteAcademicSelectBatch({
        bid: menuData?.u_batch?._id,
        did: did,
      })
        .then(() => {
          instituteDepartmentDashboardRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  console.info("Asfdsfg", menuData);
  return (
    <ModalMenu onClose={onClose} disabled={disabled}>
      <h6 onClick={onSelectBatch}>{t("select_batch")}</h6>
    </ModalMenu>
  );
};

export default InstituteAcademicDepartmentBatchCardMenu;
