import style from "../../../InstituteFeedback/InstituteFeedback.module.css";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import InstituteAcademicDepartmentBatchCardMenu from "./InstituteAcademicDepartmentBatchCardMenu";
import { useState } from "react";
const InstituteAcademicDepartmentBatchCard = ({
  batch,
  did,
  selectBatchId,
}) => {
  const [menuData, setMenuData] = useState("");

  const onClose = () => {
    setMenuData("");
  };
  const onMenu = () => {
    setMenuData(batch);
  };

  return (
    <>
      <div
        className={
          selectBatchId === batch?.u_batch?._id
            ? `${style.iadcc_card_hover} ${style.sf_card_active}`
            : style.iadcc_card_hover
        }
      >
        <div className={style.sf_card_contatiner}>
          <img
            src={`${assestsUserFinanceUrl}/batch.svg`}
            loading="lazy"
            alt="avatar"
            style={{
              borderRadius: "0",
              height: "2rem",
              width: "2rem",
            }}
          />

          <div className={style.sf_card_contatiner_inner}>
            <h6>{batch?.u_batch?.batchName ?? ""}</h6>
          </div>
        </div>

        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onMenu}
        />
      </div>
      {menuData && (
        <InstituteAcademicDepartmentBatchCardMenu
          onClose={onClose}
          menuData={menuData}
          did={did}
        />
      )}
    </>
  );
};

export default InstituteAcademicDepartmentBatchCard;
